import Helper from '@/function.js';



export default {

	data() {
		return {
			total: 0,
			token: '',
			state: '',
			taskList: [],
			pageIndex: 1,
			pageSize: 15,
			indicatorDots: true,
			phone: '拨打客服电话：400-0515-207',
			autoplay: true,
			interval: 2000,
			duration: 500,
			msg: ["111", "222"],
			sosuo: "none",
			taskNo: ""
		}
	},

	onLoad: function() {
		this.token = uni.getStorageSync('token');

	},
	onShow: function() {
		this.pageIndex = 1;
		this.taskList = [];
		//this.getNewsList();
		this.userInfo = uni.getStorageSync('userInfo');
		this.getTaskList();
	},
	onReachBottom: function() {
		if (this.taskList.length < this.total) {
			this.pageIndex++;
			this.getTaskList();
		}
	},
	onPullDownRefresh: function() {
		this.pageIndex = 1;
		this.taskList = [];
		this.getTaskList();
	},
	methods: {
		change(e) {
			this.current = e.detail.current;
		},
		goMerc() {
			uni.navigateTo({
				url: '/pages/index/mch'
			});
		},
		goFriend() {
			uni.navigateTo({
				url: 'pages/invite/invite'
			});
		},
		goNewDetails(item) {
			uni.navigateTo({
				url: '/pages/news/details?id=' + item.id
			});
		},
		news() {
			uni.navigateTo({
				url: '/pages/news/new'
			});
		},
		getInptValue: function() {

			var _this = this;
			if (_this.taskNo.length >= 1) {
				_this.sosuo = "block";
			} else {
				_this.sosuo = "none";

			}

		},
		task(val) {
			uni.navigateTo({
				url: '/pages/task/task?rank=' + val
			});
		},
		live() {
			// uni.switchTab({
			// 	url: '/pages/live/live'
			// })
			uni.navigateTo({
				url: '/pages/index/qubo'
			});
		},

		invite() {
			uni.navigateTo({
				url: '/pages/invite/invite'
			});
		},
		getGenduo() {
			// uni.switchTab({
			// 	url: '/pages/live/live'
			// })
			uni.navigateTo({
				url: '/pages/index/more'
			});
		},

		getCall() {
			// wx.makePhoneCall({
			// 	phoneNumber: this.phone
			// });
			// uni.showModal({
			// 	title: '提示',
			// 	content: '客服系统正在更新',
			// 	confirmColor: '#5A83E9',
			// 	showCancel: false,
			// });
			uni.navigateTo({
				url: '/pages/index/service'
			})
		},
		getNewsList() {
			var _this = this
			var params = {
				pageIndex: 1,
				pageSize: 5,
			}
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetNewsList',
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					//console.log(res);
					if (Helper.CheckData(res)) {
						_this.msg = res.data.list;
					}
				},
				fail: function() {
					//uni.hideLoading();
				},
				complete: function() {
					//uni.hideLoading();
					uni.stopPullDownRefresh()
				}


			})

		},

		/* 获得所有任务列表 */
		getTaskList() {
			var _this = this
			var params = {
				state: _this.state,
				pageIndex: _this.pageIndex,
				pageSize: _this.pageSize,
				taskNo: _this.taskNo
			}
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetTaskList',
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {

					if (Helper.CheckData(res)) {
						_this.taskList = _this.taskList.concat(res.data.list);
						_this.total = res.data.total;
						//console.log(_this.taskList);
					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
					uni.stopPullDownRefresh()
				}


			})

		},
		sosuoClick() {
			this.taskList = [];
			this.getTaskList();
			this.taskNo = "";
			this.sosuo = "none";
		},
		/* 领取任务移除 */
		getTask(index, taskId) {
			var _this = this;

			uni.showLoading({
				title: "加载中..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/MemberGetTask?taskId=' + taskId,
				header: {
					Authorization: _this.token
				},
				method: 'POST',
				dataType: 'json',
				success: function(res) {

					if (Helper.CheckData(res)) {
						res = res.data;
						_this.taskList.splice(index, 1);
						uni.showModal({
							title: '提示',
							content: '领取成功',
							confirmColor: '#5A83E9',
							showCancel: false,
						});
					} else {
						uni.showToast({
							title: res.data.errorMessage,
							icon: "none"
						})
					}

				},
				complete() {
					uni.hideLoading();
				},
				fail: function(res) {
					uni.stopPullDownRefresh();
				}
			});



		},
		mytasorder(item) {
			console.log("==========")
			//console.log(item)
			console.log("==========")

			uni.navigateTo({
				url: '/pages/mytask/mytaskorder?item=' + JSON.stringify(item)
			})


		}
		,onClickSwiper(id){
			
			uni.navigateTo({
				url: '/pages/news/details?id='+id
			})
			
		}
		
		
	
	},

};
